import React, { useState } from 'react';
import { range, cloneDeep } from 'lodash';
import { strategyGameFactory } from '../../strategy-game';
import { getGameStateAfterMove, getGameStateAfterAiTurn, playerColor } from './strategy/strategy';
import { generateEmptyTicTacToeBoard } from '../helpers';

const GameBoard = ({ board, setBoard, ctx }) => {
  const isDuringFirstMove = board => board.filter(c => c).length <= 1;

  const isMoveAllowed = (id) => {
    if (!ctx.shouldPlayerMoveNext) return false;
    return board[id] === null;
  };
  const clickField = (id) => {
    if (!isMoveAllowed(id)) return;

    const newBoard = cloneDeep(board);
    newBoard[id] = playerColor(ctx.playerIndex);
    setBoard(newBoard);
    if (isDuringFirstMove(newBoard)) return;
    ctx.endPlayerTurn(getGameStateAfterMove(newBoard));
  };
  const pieceColor = (id) => {
    const colorCode = board[id];
    if (colorCode === 'red') return 'bg-red-600';
    return 'bg-blue-600';
  };

  return (
  <section className="p-2 shrink-0 grow basis-2/3">
    <div className="grid grid-cols-3 gap-0 border-2">
      {range(9).map(id => (
        <button
          key={id}
          disabled={!isMoveAllowed(id)}
          onClick={() => clickField(id)}
          className="aspect-square p-[25%] border-2"
        >
          {board[id] && (
            <span
              className={`w-full aspect-square inline-block rounded-full mb-[-0.5rem] ${pieceColor(id)}`}
            ></span>
          )}
      </button>
      ))}
    </div>
  </section>
  );
};

const getPlayerStepDescription = ({ board }) => {
  const isDuringFirstMove = board.filter(c => c).length <= 1;
  return isDuringFirstMove
    ? 'Helyezz le kt korongot (kattints a mezkre)'
    : 'Helyezz le egy korongot egy res mezre kattintssal.';
};

const rule = <>
  A 3  3-as duplnkezd amba jtkban elszr a kezd tesz le kt piros korongot, majd
  a msodik egy kk korongot s innentl felvltva egy-egy korongot tesznek le a sajt sznkbl, amg
  be nem telik a tbla. A kezd nyer, ha a jtk vgn van valahol hrom piros egy sorban, oszlopban
  vagy tlban, de sehol sincs hrom kk egy sorban, oszlopban vagy tlban; egybknt a msodik
  nyer.

  Te dntheted el, hogy kezdeni szeretnl-e, vagy msodik jtkos lenni.
  Sok sikert! :)
</>;

const Game = strategyGameFactory({
  rule,
  title: 'Dupln kezd 3x3 amba',
  GameBoard,
  G: {
    getPlayerStepDescription,
    generateNewBoard: generateEmptyTicTacToeBoard,
    getGameStateAfterAiTurn
  }
});

export const TicTacToeDoubleStart = () => {
  const [board, setBoard] = useState(generateEmptyTicTacToeBoard());

  return <Game board={board} setBoard={setBoard} />;
};
