import React, { useState } from 'react';
import { strategyGameFactory } from '../strategy-game';
import { getGameStateAfterAiTurn  } from './strategy/strategy';
import { GameBoard, getPlayerStepDescription } from './coin-3-piles';

const rule = <>
  Egy kupacban 3 darab 1, 5 darab 2 s 7 darab 3 pengs rme van. Egy lpsben az
  ppen soron lv jtkos elvesz egy rmt a kupacbl, s helyette berakhat egy darab kisebb
  rtk rmt, vagy dnthet gy, hogy nem tesz be semmit. Az nyer, aki elveszi az utols rmt
  a kupacbl.

  Te dntheted el, hogy kezdeni szeretnl-e, vagy msodik jtkos lenni.
  Sok sikert! :)
</>;

const Game = strategyGameFactory({
  rule,
  title: '3 db 1, 5 db 2 s 7 db 3 pengs',
  GameBoard,
  G: {
    getPlayerStepDescription,
    generateNewBoard: () => ([3, 5, 7]),
    getGameStateAfterAiTurn
  }
});

export const Coin357 = () => {
  const [board, setBoard] = useState([3, 5, 7]);

  return <Game board={board} setBoard={setBoard} />;
};
