import React from 'react';
import { Link, useRouteError } from 'react-router-dom';

export const ErrorPage = () => {
  const error = useRouteError();

  if (error && error.status === 404) {
    return <main className="p-2">
      <p>
        Sajnljuk, a keresett oldal nem tallhat. Az albbi gombra kattintva vlassz egy jtkot.
      </p>
      <Link
        className="rounded-lg px-2 py-1 bg-blue-400 hover:bg-blue-600 text-black"
      >Vissza a foldalra</Link>
    </main>;
  }
  return (
    <main id="error-page">
      <p>Sajnljuk, egy vratlan hiba trtnt</p>
      <Link to='/'>Vissza a foldalra</Link>
    </main>
  );
};
