import React, { useState } from 'react';
import { random } from 'lodash';
import { strategyGameFactory } from '../strategy-game';
import { getGameStateAfterAiTurn  } from './strategy/strategy';
import { GameBoard, getPlayerStepDescription } from './coin-3-piles';

const generateNewBoard = () => {
  const board = [random(0, 6), random(0, 6), random(0, 6)];
  if (board[1] !== 0 || board[2] !== 0) return board;
  return generateNewBoard();
};

const rule = <>
  Van egy kupacban nhny rme, mindegyik 1, 2 vagy 3 pengs. Egy lpsben az
  ppen soron lv jtkos elvesz egy rmt a kupacbl, s helyette berakhat egy darab kisebb
  rtk rmt, vagy dnthet gy, hogy nem tesz be semmit. Az nyer, aki elveszi az utols rmt
  a kupacbl.

  Az j jtk gombra kattintva generlhatsz egy j kezdllst. A kezdlls ismeretben te dntheted el, hogy kezdeni
  szeretnl-e, vagy msodik jtkos lenni.
  Sok sikert! :)
</>;

const Game = strategyGameFactory({
  rule,
  title: '3, 2, 1 rmk',
  GameBoard,
  G: {
    getPlayerStepDescription,
    generateNewBoard,
    getGameStateAfterAiTurn
  }
});

export const Coin123 = () => {
  const [board, setBoard] = useState(generateNewBoard());

  return <Game board={board} setBoard={setBoard} />;
};
